            <div :class="classeBilancio(false)">
              {{ classeBilancio(false).toUpperCase() }}: {{ bilancioConUtente[`${classeBilancio(false)}`] }} &euro;
            </div>
            Visualizza le spese in comune con {{ bilancioConUtente.nome }}: 
            <input type="checkbox" @click="speseConUtenteCercato" id="bottone-switch" class="bottone-switch">
            <label for="bottone-switch" class="toggle-label"></label>

            <div v-if="mostraSpeseCondivise">Numero spese in comune: {{ contaSpeseInComune }} </div>
          </div>
        </div>
      </div>
      
    </div> 
    <div class="lista-spese">
      <div v-for="(speseMese, meseAnno) in speseFiltrate" :key="speseMese.id">  
        <div class="mese-separator">{{ meseAnno }}</div>
          <div v-for="(spesa, index) in speseMese" :key="spesa.id" class="spesa-item">
            <div class="creatore">|{{index+1}}| {{ spesa.nome_creatore }} {{ spesa.cognome_creatore }}</div>
            <div class="costo-totale">{{ spesa.costo_totale }} &euro;</div>
            <div class="info">
              <div class="info-spesa">
                <div class="categoria">
                  <img :src="require(`@/assets/immagini/${spesa.categoria}.png`)" :alt="spesa.categoria" class="immagine-categoria">
                  <u>{{ spesa.categoria }}</u>
                </div>
                <div class="descrizione">{{ spesa.descrizione }}</div>
              </div>  
              <div class="partecipanti">
                <ul>
                  <li style="border-bottom: 1px solid #ddd;" :class="{ 'creatore-evidenziato': spesa.nome_creatore === utenteLoggato.nome && spesa.cognome_creatore === utenteLoggato.cognome }">Creatore <span class="quota-partecipante">{{ spesa.utente_creatore.quota }} &euro;</span></li>
                  <li v-for="partecipante in spesa.partecipanti" :key="partecipante.utente" class="partecipante" :class="{ 'creatore-evidenziato': partecipante.nome === utenteLoggato.nome && partecipante.cognome === utenteLoggato.cognome }">
                    {{ partecipante.username }} <span class="quota-partecipante">{{ partecipante.quota }} &euro;</span>
                  </li>
                </ul>
              </div> 
            </div>   
            <div class="bottoni-spesa">
              <button @click="apriModificaSpesa(spesa)">Modifica</button>
              <button @click="apriEliminaSPesa(spesa)">Elimina</button>
            </div>
            <div v-if="mostraModificaSpesa" class="spesa-modifica active">
              <div class="pannello-modifica active">
                  <div class="form-di-modifica">
                    <h3>Modifica spesa</h3>

                    <div class="campo-form">
                      <label for="categoria-select">Seleziona Categoria</label>
                      <select id="categoria-select" v-model="spesaDaModificare.categoria" required @click="controllaCostoTotaleModifica">
                        <option value="" disabled selected hidden>Seleziona categoria</option>
                        <option v-for="categoria in categorie" :key="categoria.nome" :value="categoria.nome">
                          {{ categoria.nome }}
                        </option>
                      </select>
                    </div>

                    <div class="campo-form">
                      <label for="descrizione-input">Descrizione</label>
                      <input id="descrizione-input" v-model="spesaDaModificare.descrizione" type="text" placeholder="Inserisci descrizione">
                    </div>

                    <div class="campo-form">
                      <label for="costo-input">Costo Totale</label>
                      <input id="costo-input" v-model="spesaDaModificare.costo_totale" type="number" required :disabled="categoriaRimborso" placeholder="Inserisci costo totale">
                    </div>

                    <div class="campo-form">
                      <label for="categoria-input">Tua quota</label>
                      
                      <input v-if="!categoriaRimborso && !isEmpty(spesaDaModificare.partecipanti)" id="quota-creatore" v-model="spesaDaModificare.utente_creatore.quota" type="number" placeholder="Inserisci la tua quota" :disabled="isEmpty(spesaDaModificare.partecipanti) || categoriaRimborso">
                      <input v-else-if="isEmpty(spesaDaModificare.partecipanti)" id="quota-creatore" v-model="spesaDaModificare.costo_totale" type="number" placeholder="Inserisci la tua quota" :disabled="isEmpty(spesaDaModificare.partecipanti) || categoriaRimborso">
                      <input v-else id="quota-creatore" v-model="quota_creatore_rimborso" type="number" placeholder="Inserisci la tua quota" :disabled="categoriaRimborso">
                      
                    </div>

                    <div class="campo-form">
                      <label for="input-data">Data</label>
                      <input id="input-data" v-model="spesaDaModificare.data" type="month" required>
                    </div>
                    
                    <div class="ricerca-utente">
                      <label for="barra-cerca-partecipanti">Modifica partecipanti</label>
                      <div class="input-container">
                        <input v-model="cercaPartecipante" type="text" @focus="attivaMenuPartecipanti(true)" @blur="attivaMenuPartecipanti(false)" id="barra-cerca-partecipanti" :disabled="categoriaRimborso && !isEmpty(partecipanti)" placeholder="Cerca partecipanti">
                        <span v-if="cercaPartecipante" class="clear-button" @click="annullaRicercaPartecipanti">✖</span>
                      </div>
                      <ul v-if="tendinaPartecipanti && filtraPartecipanti.length" class="dropdown-list">
                        <li v-for="partecipante in filtraPartecipanti" :key="partecipante.id" @mousedown="mostraPartecipante(partecipante)">
                          {{ partecipante.username }} - {{ partecipante.nome }} {{ partecipante.cognome }}
                        </li>
                      </ul>
                    </div>
                    <div v-if="!isEmpty(nuovoPartecipante)" class="dettagli-con-utente">
                      <div class="credenziali"> {{ nuovoPartecipante.username }} - {{ nuovoPartecipante.nome }} {{ nuovoPartecipante.cognome }} <span class="clear-button" @click="nuovoPartecipante={}"> ✖</span></div>
                      <input v-model="quota_partecipante" type="number" class="quota-partecipante" placeholder="quota">
                      <button :disabled="!quota_partecipante" @click="addPartecipanteModifica(nuovoPartecipante)">Aggiungi a partecipanti</button>
                    </div>
                    <div v-if="spesaDaModificare.partecipanti.length > 0">
                      <div class="somma-quote-partecipanti"><span style="font-weight: bold;"> Somma quote partecipanti: {{ sommaQuotePartecipanti }}</span></div>
                      <div v-for="partecipante in spesaDaModificare.partecipanti" :key="partecipante.utente" class="dettagli-con-utente">
                        {{ credenzialiUtente(partecipante.utente).username }}: {{ partecipante.quota }} &euro;<span class="clear-button" @click="rimuoviPartecipanteModifica(partecipante.utente)">✖</span>
                      </div>
                    </div>

                    <div class="bottoni-modifica">
                      <button type="submit" @click="modificaSpesa" class="bottone-modifica-spesa">Salva Modifiche</button>
                      <button type="button" @click="chiudiModificaSpesa" class="bottone-modifica-spesa">Annulla</button>
                    </div>
                  </div>
              </div>
            </div>
            <div v-if="mostraEliminaSpesa" class="spesa-elimina active"> 
              <div class="pannello-modifica active">
                <h4>Sei sicuro di voler eliminare la spesa?</h4>
                <div class="bottoni-elimina-spesa">
                  <button type="button" @click="eliminaSpesa" class="bottone-elimina">Si</button>
                  <button type="button" @click="chiudiEliminaSpesa" class="bottone-elimina">No</button>
                </div>
              </div>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'BudgetPage',
  data() {
    return {
      utenteLoggato: {},
      bilancioUtenteLoggato: {},
      rgb: '',
      annoSelezionato: '', 
      elencoAnni: [],
      elencoMesi: [],
      meseSelezionato: '',
      categoria: '',
      categoriaRimborso: false,
      descrizione: '',
      costo_totale: '',
      partecipanti: [],
      partecipantiFiltrati: [],
      nuovoPartecipante: {},
      quota_partecipante: '',
      quota_creatore: '',
      quota_creatore_rimborso: '',
      sommaQuotePartecipanti: 0,
      data: new Date().toISOString().slice(0, 7),
      mese: '',
      ricercaSpese: '',
      spese: [],
      categorie: [],
      cercaPartecipante: '',
      cercaUtente: '',
      bilancioConUtente: {},
      mostraSpeseCondivise: false,
      utenti: [],
      utentiPerRicerca: [],
      tendinaPartecipanti: false,
      tendinaUtente: false,
      tendina1: false,
      tendina2: false,
      tendina3: false,
      tendina4: false,
      speseOrdinate: {},
      copiaSpeseOrdinate: {},
      piuRecente: true,
      selectedCategorie: [],
      mostraModificaSpesa: false,
      mostraEliminaSpesa: false,
      spesaDaModificare: {},
      spesaDaEliminare: {}
    };
  },  
  mounted() {
    this.getSpese();
    this.getCategorie();
    this.getUtenti();
    this.getInfo();
    this.getBilancio();
    this.generaSfondoImmagine();
  },
  methods: {
    addSpesa: async function() {
      try {
        const quota_creatore_parsata = parseFloat(document.getElementById('quota-creatore').value);
        const costo_totale_parsato = parseFloat(this.costo_totale);
        const anno = this.data.substring(0, 4);
        const mese = this.data.substring(5, 7);
        const partecipantiCopia = [... this.partecipanti];
        partecipantiCopia.forEach(partecipante => {
          delete partecipante['username'];
        });

        // Controllo che la somma delle quote corrisponda al costo totale
        if(costo_totale_parsato === quota_creatore_parsata + this.sommaQuotePartecipanti || this.categoriaRimborso) {
          const nuovaSpesa = {
            categoria: this.categoria,
            descrizione: this.descrizione,
            costo_totale: costo_totale_parsato,
            quota_creatore: quota_creatore_parsata,
            partecipanti: partecipantiCopia
          };
          
          const response = await axios.post(`/api/budget/${anno}/${mese}`, nuovaSpesa);
          console.log('Spesa aggiunta con successo:', response.data);

          // Aggiorno la lista delle spese dopo aver aggiunto una nuova spesa e richiedo il bilancio
          this.speseOrdinate = {};
          this.getSpese();
          this.getBilancio();
        }
        else alert('La somma delle quote non corrisponde!');
  
        this.partecipanti.forEach(partecipante => {
          const utenteDaRiaggiungere = this.utenti.find(u => u.id === partecipante.utente);
          
          if(utenteDaRiaggiungere)
          this.partecipantiFiltrati.push(utenteDaRiaggiungere);
        });

        this.resettaCreazioneSpesa(true);

      } catch (error) {
        alert('Errore durante l\'aggiunta della spesa:', error);
      }
    },
    apriModificaSpesa: function(spesa) {
      this.resettaCreazioneSpesa(false);
      this.spesaDaModificare =  JSON.parse(JSON.stringify(spesa));
      this.data = this.spesaDaModificare.data;
      
      if(!this.isEmpty(this.spesaDaModificare.partecipanti)) {
        this.spesaDaModificare.partecipanti.forEach(partecipante => {
          this.sommaQuotePartecipanti += partecipante.quota;
        });
      }
      this.mostraModificaSpesa = true;
    },
    apriEliminaSPesa: function(spesa) {
      this.spesaDaEliminare = spesa;
      this.mostraEliminaSpesa = true;
    },
    chiudiModificaSpesa: function() {
      this.mostraModificaSpesa = false;
      this.spesaDaModificare = {};
      
      this.resettaCreazioneSpesa(false);
    },
    chiudiEliminaSpesa: function() {
      this.mostraEliminaSpesa = false;
    },
    modificaSpesa: async function() {
      try {
        const spesa = this.spesaDaModificare;
        
        const [anno, mese] = this.data.split('-');
        const response = await axios.put(`/api/budget/${anno}/${mese}/${spesa.id}`, spesa);
        console.log('Risposta PUT:', response.data);

        this.chiudiModificaSpesa();
        this.speseOrdinate = {};
        //aggiorno in this.spese la spesa modificata e richiedo il bilancio
        this.getSpese();
        this.getBilancio();
      } catch (error) {
        console.error('Errore durante la modifica della spesa:', error);
        alert('Si è verificato un errore durante la modifica della spesa');
    }
      
    },
    eliminaSpesa: async function() {
      try {
        const spesa = this.spesaDaEliminare;
        if (!spesa) {
          throw new Error('Spesa non trovata');
        }

        const [anno, mese] = spesa.data.split('-');

        // Invia la richiesta al server
        const response = await axios.delete(`/api/budget/${anno}/${mese}/${spesa.id}`);
        
        if (!response.data.eliminata) {
          throw new Error('Errore nella cancellazione della spesa');
        }

        // Rimuovi la spesa dalla lista locale
        this.spese = this.spese.filter(sp => sp.id !== spesa.id);
       
        this.spesaDaEliminare = {};
        this.speseOrdinate = {};

        //riaggiorno il bilancio e le spese
        this.getSpese();
        this.getBilancio();

        this.chiudiEliminaSpesa();
        
      } catch (error) {
        console.error('Errore:', error);
        alert('Si è verificato un errore durante la cancellazione della spesa');
      }
    },
    getInfo: async function() {
      try {
        const response = await axios.get('/api/budget/whoami');
        this.utenteLoggato = response.data;

      } catch (error) {
        console.error('Errore durante il recupero dei dati dell\'utente loggato:', error);
      }
    },
    getSpese: async function() {
      try {
        const response = await axios.get('/api/budget/');
        this.spese = response.data;

        this.ordinaSpesePerData();
      } catch (error) {
        console.error('Errore durante il recupero delle spese:', error);
      }
    },
    getCategorie: async function() {
      const response = await axios.get('/api/budget/categorie');
        this.categorie = response.data;
    },
    getUtenti: async function() {
      try {
        const response = await axios.get('/api/budget/utenti');
        this.utenti = response.data;
        this.partecipantiFiltrati =  JSON.parse(JSON.stringify(this.utenti));
        
        this.utentiPerRicerca =  JSON.parse(JSON.stringify(this.utenti));
      } catch (error) {
        console.error('Errore durante il recupero degli utenti:', error);
      }
    },
    getBilancio: async function() {
      try {
        const response = await axios.get('/api/balance/');
        this.bilancioUtenteLoggato = response.data;
      } catch (error) {
        console.error('Errore durante il recupero del bilancio dell\'utente loggato', error);
      }
    },
    infoUtenteCercato: async function(utente) {
      try {
        const response = await axios.get(`/api/balance/${utente.id}`);
        this.bilancioConUtente = response.data;
        this.bilancioConUtente.username = utente.username;
        this.bilancioConUtente.nome = utente.nome;
        this.bilancioConUtente.cognome = utente.cognome;
      } catch (error) {
        console.error('Errore durante il recupero del bilancio tra utente loggato e un altro utente:', error);
      }
    },
    ordinaSpesePerData: function() {
      const spesePerMese = {};

      this.spese.forEach(spesa => {
        const meseAnno = spesa.data.substring(0, 7);
        if (!spesePerMese[meseAnno]) {
          spesePerMese[meseAnno] = [];
        }
        spesePerMese[meseAnno].push(spesa);
      });

      const dateOrdinate = Object.keys(spesePerMese).sort();
      
      // Setto l'elenco degli anni
      this.elencoAnni = [...new Set(dateOrdinate.map(data => data.substring(0, 4)))];
      this.elencoMesi = [...new Set(dateOrdinate.map(data => data.substring(5, 7)))].sort();


      dateOrdinate.forEach(dataOrdinata => {
        this.speseOrdinate[dataOrdinata] = spesePerMese[dataOrdinata];
      });
    },
    resettaCreazioneSpesa: function(tieniAperto) {
      this.costo_totale = '';
      this.quota_creatore = '';
      this.nuovoPartecipante = {};
      this.partecipanti.length = 0;
      this.sommaQuotePartecipanti = 0;
      this.data = new Date().toISOString().slice(0, 7);
      if(!tieniAperto)
        this.tendina3 = false; 
    },
    controllaCostoTotale: function() {
      if (this.categoria==='Rimborso') {
        this.categoriaRimborso = true;
        this.costo_totale = 0;
        
        //nel caso in cui venga impostata la categoria rimborso con partecipanti non nullo, lo svuoto
        if(! this.isEmpty(this.partecipanti)) {
          this.partecipanti.forEach(partecipante => {
            const utenteDaRiaggiungere = this.utenti.find(u => u.id === partecipante.utente);
      
            if(utenteDaRiaggiungere)
            this.partecipantiFiltrati.push(utenteDaRiaggiungere);
          });
          
          this.partecipanti.length = 0;
          
        }
      }
      else 
        this.categoriaRimborso = false;
    },
    controllaCostoTotaleModifica: function() {
      if (this.spesaDaModificare.categoria==='Rimborso') {
        this.categoriaRimborso = true;
        this.spesaDaModificare.costo_totale = 0;
        
        //nel caso in cui venga impostata la categoria rimborso con partecipanti non nullo, lo svuoto
        if(! this.isEmpty(this.spesaDaModificare.partecipanti)) {
          this.spesaDaModificare.partecipanti.forEach(partecipante => {
            const utenteDaRiaggiungere = this.utenti.find(u => u.id === partecipante.utente);
      
            if(utenteDaRiaggiungere)
            this.partecipantiFiltrati.push(utenteDaRiaggiungere);
          });
          
          this.spesaDaModificare.partecipanti.length = 0;
        }
      }
      else 
        this.categoriaRimborso = false;
    },
    mostraPartecipante: function(partecipante) {
      this.nuovoPartecipante = {
          utente: partecipante.id,
          username: partecipante.username,
          nome: partecipante.nome,
          cognome: partecipante.cognome
      };
      this.cercaPartecipante = ''; 
      this.tendinaPartecipanti = false;
    },
    addPartecipante: function(partecipante) {
      if (!this.partecipanti.some(p => p.utente === partecipante.utente)) {
        this.partecipanti.push({
          utente: partecipante.utente,
          username: partecipante.username,
          quota: parseFloat(this.quota_partecipante)
        });
        
        // Se si tratta di un rimborso tra l'utente loggato e un altro utente faccio in modo che abbiano le quote opposte
        if(this.categoriaRimborso) 
          this.quota_creatore_rimborso = this.quota_partecipante*-1; 
        else this.sommaQuotePartecipanti += parseFloat(this.quota_partecipante);

        this.nuovoPartecipante = {};
        this.quota_partecipante = '';

        const indiceDaRimuovere = this.partecipantiFiltrati.findIndex(partecipanteFiltrato => partecipanteFiltrato.id === partecipante.utente);
       
        if (indiceDaRimuovere !== -1) 
          this.partecipantiFiltrati.splice(indiceDaRimuovere, 1);
      }
    },
    addPartecipanteModifica: function(partecipante) {
      if (!this.spesaDaModificare.partecipanti.some(p => p.utente === partecipante.utente)) {
        this.spesaDaModificare.partecipanti.push({
          utente: partecipante.utente,
          quota: parseFloat(this.quota_partecipante)
        });
        
        // Se si tratta di un rimborso tra l'utente loggato e un altro utente faccio in modo che abbiano le quote opposte
        if(this.categoriaRimborso) 
          this.quota_creatore_rimborso = this.quota_partecipante*-1; 
        else this.sommaQuotePartecipanti += parseFloat(this.quota_partecipante);

        this.nuovoPartecipante = {};
        this.quota_partecipante = '';

        const indiceDaRimuovere = this.partecipantiFiltrati.findIndex(partecipanteFiltrato => partecipanteFiltrato.id === partecipante.utente);
        
        if (indiceDaRimuovere !== -1) 
          this.partecipantiFiltrati.splice(indiceDaRimuovere, 1);
      }
    },
    rimuoviPartecipante: function(utente) {
      const indiceDaRimuovere = this.partecipanti.findIndex(partecipante => partecipante.utente === utente);
      const partecipanteDaRimuovere = this.partecipanti.find(partecipante => partecipante.utente === utente);

      //riaggiungo nell'elenco dei partecipanti da poter scegliere il partecipante che si sta togliendo dalla lista dei partecipanti
      const utenteDaRiaggiungere = this.utenti.find(u => u.id === utente);
      
      //azzero o tolgo dalla sommaquota la quota dell'utente
      if(this.categoriaRimborso)
        this.sommaQuotePartecipanti = 0;
      else 
        this.sommaQuotePartecipanti -= partecipanteDaRimuovere.quota;
      
      if(utenteDaRiaggiungere)
        this.partecipantiFiltrati.push(utenteDaRiaggiungere);

      if (indiceDaRimuovere !== -1) 
          this.partecipanti.splice(indiceDaRimuovere, 1);   
      
    },
    rimuoviPartecipanteModifica: function(utente) {
      const indiceDaRimuovere = this.spesaDaModificare.partecipanti.findIndex(partecipante => partecipante.utente === utente);
      const partecipanteDaRimuovere = this.spesaDaModificare.partecipanti.find(partecipante => partecipante.utente === utente);


      //riaggiungo nell'elenco dei partecipanti da poter scegliere il partecipante che si sta togliendo dalla lista dei partecipanti
      const utenteDaRiaggiungere = this.utenti.find(u => u.id === utente);
        
      //azzero o tolgo dalla sommaquota la quota dell'utente
      if(this.categoriaRimborso)
        this.sommaQuotePartecipanti = 0;
      else 
        this.sommaQuotePartecipanti -= partecipanteDaRimuovere.quota;

      if(utenteDaRiaggiungere)
        this.partecipantiFiltrati.push(utenteDaRiaggiungere);

      if (indiceDaRimuovere !== -1) 
          this.spesaDaModificare.partecipanti.splice(indiceDaRimuovere, 1);   
      
    },
    annullaRicercaSpese: function() {
      this.ricercaSpese = "";
    },
    annullaRicercaPartecipanti: function() {
      this.cercaPartecipante = '';
      this.tendinaPartecipanti = false;
    },
    annullaRicercaUtente: function() {
      this.cercaUtente = '';
      this.tendinaUtente = false;
    },
    attivaMenuPartecipanti: function(veroFalso) {
      this.tendinaPartecipanti = veroFalso;
    },
    attivaMenuUtente: function(veroFalso) {
      this.tendinaUtente = veroFalso;
    },
    apriTendina: function(tendina) {
      if(tendina === 'tendina1') 
        this.tendina1 = !this.tendina1;
      else if(tendina === 'tendina2') 
        this.tendina2 = !this.tendina2;
      else if(tendina === 'tendina3')
        this.tendina3 = !this.tendina3;
      else this.tendina4 = !this.tendina4;

    },
    isEmpty: function(oggetto) {
      if(typeof oggetto === 'string')
        return oggetto.length === 0;
      else
        return Object.keys(oggetto).length === 0;
    },
    invertiOrdineSpese: function() {
      // Cambio il valore della variabile del bottone quando viene cliccato
      this.piuRecente = ! this.piuRecente;

      let chiavi = Object.keys(this.speseOrdinate);
     
      // Inverti l'array delle chiavi
      let chiaviInvertite = chiavi.reverse();

      // Inverto l'ordine delle spese
      let speseOrdinateInvertite = {};
      
      chiaviInvertite.forEach(chiave => {
        speseOrdinateInvertite[chiave] = this.speseOrdinate[chiave];
      });

      // Aggiorno speseOrdinate
      this.speseOrdinate = speseOrdinateInvertite;

      // Nel caso in cui l'utente loggato esegua queste operazioni
      //  - selezionare la spunta per vedere le spese in comune con un altro utente
      //  - ordinare le spese dalla più recente
      //  - togliere la spunta 
      // Le spese sarebbero ricomparse tutte ma dalla meno recente.
      // Per risolvere il problema riordino anche la copia di speseOrdinate quando 
      // this.mostraSpeseCondivise è true
      
      if(this.mostraSpeseCondivise) {
        let chiaviCopia = Object.keys(this.copiaSpeseOrdinate);
      
        let chiaviCopiaInvertite = chiaviCopia.reverse();

        let speseCopiaOrdinateInvertite = {};
        
        chiaviCopiaInvertite.forEach(chiave => {
          speseCopiaOrdinateInvertite[chiave] = this.copiaSpeseOrdinate[chiave];
        });

        this.copiaSpeseOrdinate = speseCopiaOrdinateInvertite;
      }
    },
    speseConUtenteCercato: function() {
      this.mostraSpeseCondivise = ! this.mostraSpeseCondivise;
      
      if (this.mostraSpeseCondivise) {
        // Salvo una copia di speseOrdinate
        this.copiaSpeseOrdinate = this.speseOrdinate;

        let chiavi = Object.keys(this.speseOrdinate);
        let speseConUtenteOrdinate = {};

        chiavi.forEach(chiave => {
          let spese = this.speseOrdinate[chiave];
          
          // Filtra le spese per ottenere solo quelle in comune con l'utente cercato
          let speseComuni = spese.filter(spesa => {
            return this.bilancioConUtente.speseConUtente.some(spesaConUtente => spesaConUtente.id === spesa.id);
          });

          if (speseComuni.length > 0) 
            speseConUtenteOrdinate[chiave] = speseComuni;
        });
    
        this.speseOrdinate = speseConUtenteOrdinate;
      } 
      else 
        this.speseOrdinate = this.copiaSpeseOrdinate;
    },
    classeBilancio: function(infPerUtenteLoggato) {
      if(infPerUtenteLoggato) 
        return 'credito' in this.bilancioUtenteLoggato ? 'credito' : 'debito';
      else
        return 'credito' in this.bilancioConUtente ? 'credito' : 'debito';
    },
    generaSfondoImmagine: function() {
      let red = Math.floor(Math.random() * 256);
      let green = Math.floor(Math.random() * 256);
      let blue = Math.floor(Math.random() * 256);
      let media = red * 0.299 + green * 0.587 + blue * 0.114;

      while (media < 128) {
        red = Math.floor(Math.random() * 256);
        green = Math.floor(Math.random() * 256);
        blue = Math.floor(Math.random() * 256);
        //per evitare sfondi troppo chiari
        media = red * 0.299 + green * 0.587 + blue * 0.114;
      }
      
      this.rgb = `rgb(${red}, ${green}, ${blue})`;
    },
    credenzialiUtente: function(idUtente) {
      const utente = this.utenti.find(u => u.id === idUtente);

      // Se l'utente è trovato, ritorna solo username, nome e cognome
      if (utente) {
        return {
          username: utente.username,
          nome: utente.nome,
          cognome: utente.cognome
        };
      } else return {}; 
    }
  },
  computed: {
    speseFiltrate: function() {
      const lowerQuery = this.ricercaSpese.toLowerCase();
      const categorieSelezionate = this.selectedCategorie;
      const annoSelezionato = this.annoSelezionato;
      const meseSelezionato = this.meseSelezionato;
      let speseFiltrateOrdinate = {};

      // Itera sulle chiavi dell'oggetto speseOrdinate
      Object.keys(this.speseOrdinate).forEach(key => {
        const spesePerData = this.speseOrdinate[key];
        let speseFiltrate = [];

        // Filtra le spese per descrizione o categoria
        if (lowerQuery.length > 0) {
          speseFiltrate = spesePerData.filter(spesa => {
            return (
              spesa.categoria.toLowerCase().includes(lowerQuery) ||
              spesa.descrizione.toLowerCase().includes(lowerQuery)
            );
          });
        } else {
          speseFiltrate = spesePerData.slice();
        }

        // Filtro per categorie selezionate
        if (categorieSelezionate.length > 0) {
          speseFiltrate = speseFiltrate.filter(spesa => {
            return categorieSelezionate.includes(spesa.categoria);
          });
        }

        // Filtro per anno selezionato
        if (annoSelezionato !== '') {
          speseFiltrate = speseFiltrate.filter(spesa => {
            const dataAnno = spesa.data.substring(0, 4);
            return dataAnno === annoSelezionato;
          });
        }

        //Filtro per il mese selezionato
        if (meseSelezionato !== '' && annoSelezionato) {
          speseFiltrate = speseFiltrate.filter(spesa => {
            const dataMese = spesa.data.substring(5, 7);
            
            return dataMese === meseSelezionato; 
          });
        }

        // Se ci sono spese filtrate, aggiungile all'oggetto risultato
        if (speseFiltrate.length > 0) {
          speseFiltrateOrdinate[key] = speseFiltrate;
        }
      });

      // Se non ci sono filtri e nessuna ricerca, ritorna le spese originali
      if (this.isEmpty(speseFiltrateOrdinate) && lowerQuery.length === 0 && categorieSelezionate.length === 0 && annoSelezionato === '') {
        speseFiltrateOrdinate = this.speseOrdinate;
      }

      return speseFiltrateOrdinate;
    },
    filtraUtente: function() {
      const lowerQuery = this.cercaUtente.toLowerCase();
      return this.utentiPerRicerca.filter(utente => {
        return (
          utente.username.toLowerCase().includes(lowerQuery) ||
          utente.nome.toLowerCase().includes(lowerQuery) ||
          utente.cognome.toLowerCase().includes(lowerQuery)
        );
      });
    },
    filtraPartecipanti: function() {
      const lowerQuery = this.cercaPartecipante.toLowerCase();
      return  this.partecipantiFiltrati.filter(utente => {
        return (
          utente.username.toLowerCase().includes(lowerQuery) ||
          utente.nome.toLowerCase().includes(lowerQuery) ||
          utente.cognome.toLowerCase().includes(lowerQuery)
        );
      });
    },
    contaSpeseInComune: function() {
      let speseInComune = 0;
      let chiavi = Object.keys(this.speseOrdinate);

      chiavi.forEach(chiave => {
        speseInComune += this.speseOrdinate[chiave].length;
      });

      return speseInComune;
    },
    nessunaCategoriaSelezionata() {
      return this.selectedCategorie.length === 0;
    }
  }
};
</script>

