<script>
  export default {
    name: 'FooterPage',
    data() {
        return {
            mostraAvviso: false,
            messaggioDiAiuto: '',
            mostraRisposta: false
        }
    },
    methods: {
        inviaPerFinta: function(avvisoManutenzione) {
            if(avvisoManutenzione) {
                this.mostraAvviso = true;
                setTimeout(() => {
                        this.mostraAvviso = false;
                    }, 2500);
            }
            else {
                if(this.messaggioDiAiuto !== '') {
                    this.messaggioDiAiuto = '';
                    this.mostraRisposta = true;

                    setTimeout(() => {
                        this.mostraRisposta = false;
                    }, 2500);
                }
            }
        }
    }
  };
</script>
  
