<template>
  <div class="container">
    <div class="login-form">
      <h2>Bentornato!</h2>
      <form @submit.prevent="invioDati">
        <div class="sezione-input">
          <label for="username">Username:</label>
          <input type="text" id="username" v-model="username" required>
        </div>

        <div class="sezione-input">
          <label for="password">Password:</label>
          <input type="password" id="password" v-model="password" required>
        </div>

        <button type="submit" class="btn">Accedi</button>

        <div class="signup">
          <p>Non hai un account? <router-link to="/signup">Registrati!</router-link></p>
        </div>
      </form>
      <p v-if="messaggioErrore" class="errore" style="color: red;">{{ messaggioErrore }}</p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    name: 'SignInPage',
    data() {
      return {
        username: '',
        password: '',
        messaggioErrore: ''
      };
    },
    methods: {
      invioDati: async function() {
        try {
          const loginUtente = {
            username: this.username,
            password: this.password
          }

          const response = await axios.post('/api/auth/signin', loginUtente);

          if (response.status === 200) {
            // Gestione del login avvenuto con successo
            console.log(response.msg);

            this.$router.push('/budget');
          } else {
            // Gestione degli errori di login
            this.messaggioErrore = 'Credenziali errate';
          }
        } catch (error) {
          console.error('Error:', error);
          this.error = 'Errore durante il login';
        }
      }
    }
  };
</script>

