<template>
  <div class="container">
    <div class="login-form">
      <h2>Benvenuto!</h2>
      <form @submit.prevent="invioDati">
        <div class="sezione-input">
          <label for="username">Username:</label>
          <input type="text" v-model="username"  id="username" required>
        </div>
        <div class="sezione-input">
          <label for="nome">Nome:</label>
          <input type="text" v-model="nome" id="nome" required>
        </div>
        <div class="sezione-input">
          <label for="cognome">Cognome:</label>
          <input type="text" v-model="cognome" id="cognome" required>
        </div>
        <div class="sezione-input">
          <label for="password">Password:</label>
          <input type="password" v-model="password" id="password" required>
        </div>

        <button type="submit" class="btn">Registrati</button>

        <div class="signin">
          <p>Hai già un account? <router-link to="/signin">Accedi!</router-link></p>
        </div>
      </form>
      <p v-if="messaggioErrore" class="errore" style="color: red;">{{ messaggioErrore }}</p>
    </div>
  </div>
</template>
  
<script>
  import axios from 'axios';

  export default {
    name: 'SignUpPage',
    data() {
      return {
        username: '',
        nome: '',
        cognome: '',
        password: '',
        messaggioErrore: ''
      };
    },
    methods: {
      invioDati: async function() {
        try {
          const nuovaRegistrazione = {
            username: this.username,
            nome: this.nome,
            cognome: this.cognome,
            password: this.password
          }

          const response = await axios.post('/api/auth/signup', nuovaRegistrazione);
         
          if (response.status === 200) {
             // Registrazione avvenuta con successo
             console.log(response.msg);
            
            this.$router.push('/signin');
          } else {
            this.errorMessage = response.data.msg;
          }
        } catch (error) {
          this.errorMessage = 'Errore durante la registrazione. Riprova.';
        }
      }
    }
  };
</script>
  
